@import url('https://fonts.googleapis.com/css?family=Fira+Sans:400,500,700,800');

html {
  line-height: 1.4;
  font-family: $font-family;
  @media only screen and (min-width: 0) {
    font-size: 12px;
  }

  @media only screen and (min-width: $medium-screen) {
    font-size: 14px;
  }

  @media only screen and (min-width: $large-screen) {
    font-size: 14px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: 400;
	line-height: 1.1;
}

// Header Styles
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a { font-weight: inherit; }

h1,
.h1 {
  font-size: $h1-fontsize;
  line-height: 110%;
  margin: ($h1-fontsize / 2) 0 ($h1-fontsize / 2.5) 0;
}

h2,
.h2 {
  font-size: $h2-fontsize;
  line-height: 110%;
  margin: ($h2-fontsize / 2) 0 ($h2-fontsize / 2.5) 0;
}
h3,
.h3 {
  font-size: $h3-fontsize;
  line-height: 110%;
  margin: ($h3-fontsize / 2) 0 ($h3-fontsize / 2.5) 0;
}
h4,
.h4 {
  font-size: $h4-fontsize;
  line-height: 110%;
  margin: ($h4-fontsize / 4) 0 ($h4-fontsize / 4) 0;
}
h5,
.h5 {
  font-size: $h5-fontsize;
  line-height: 110%;
  margin: ($h5-fontsize / 8) 0 ($h5-fontsize / 8) 0;
}
h6,
.h6 {
  font-size: $h6-fontsize;
  line-height: 110%;
  margin: ($h6-fontsize / 2) 0 ($h6-fontsize / 2.5) 0;
}

.text-1 {
  font-size: $h1-fontsize;
  line-height: 110%;
  margin: ($h1-fontsize / 2) 0 ($h1-fontsize / 2.5) 0;
}
.text-2 {
  font-size: $h2-fontsize;
  line-height: 110%;
  margin: ($h2-fontsize / 2) 0 ($h2-fontsize / 2.5) 0;
}
.text-3 {
  font-size: $h3-fontsize;
  line-height: 110%;
  margin: ($h3-fontsize / 2) 0 ($h3-fontsize / 2.5) 0;
}
.text-4 {
  font-size: $h4-fontsize;
  line-height: 110%;
  margin: ($h4-fontsize / 4) 0 ($h4-fontsize / 4) 0;
}
.text-5 {
  font-size: $h5-fontsize;
  line-height: 110%;
  margin: ($h5-fontsize / 8) 0 ($h5-fontsize / 8) 0;
}
.text-6 {
  font-size: $h6-fontsize;
  line-height: 110%;
  margin: ($h6-fontsize / 2) 0 ($h6-fontsize / 2.5) 0;
}

.text-l {
  font-size: 2.2rem;
  line-height: 110%;
  margin: (2.2rem / 2) 0 (2.2rem / 2.5) 0;
}
.text-xl {
  font-size: 2.4rem;
  line-height: 110%;
  margin: (2.4rem / 2) 0 (2.4rem / 2.5) 0;
}
.text-xxl {
  font-size: 2.8rem;
  line-height: 110%;
  margin: (2.8rem / 2) 0 (2.8rem / 2.5) 0;
}

p {
  -webkit-margin-before: 0;
  -webkit-margin-after: 0;
  -webkit-margin-start: 0;
  -webkit-margin-end: 0;
  margin: 0;
  padding: 3px 0;
  font-size: 1.1em;
}
// Text Styles
em {
  font-style: italic;
}
.tw-ultrabold {
  font-weight: 900;
}
.tw-bold {
  font-weight: 700;
}
strong {
  font-weight: 500;
}
small {
  font-size: 90%;
}
.light {
  font-weight: 300;
}
.thin {
  font-weight: 200;
}
.x-small {
  font-size: 70%;
}

.tw-normal {
  font-weight: 400;
}
.tw-light {
  font-weight: 300;
}

.material-icons {
  vertical-align: middle;
}

.fira {
  font-family: 'Fira Sans', sans-serif;
}



